    setMemories(state, memories) {
      state.memories = memories
    },
    setMessages(state, messages) {
      state.messages = messages
    },
    setPhotos(state, photos) {
      state.photos = photos
    },
    setWishlist(state, wishlist) {
      state.wishlist = wishlist
      // Guardar en localStorage cada vez que se actualice la lista
      localStorage.setItem('wishlist', JSON.stringify(wishlist))
    },
    toggleWishItem(state, id) {
      const item = state.wishlist.find(item => item.id === id)
      if (item) {
        item.completed = !item.completed
      }
      // Guardar en localStorage despu茅s de la modificaci贸n
      localStorage.setItem('wishlist', JSON.stringify(state.wishlist))
    },
    addWishItem(state, newWish) {
      const newId = state.wishlist.length > 0 ? state.wishlist[state.wishlist.length - 1].id + 1 : 1
      state.wishlist.push({
        id: newId,
        ...newWish,
        completed: false,
        deleted: false // Por defecto, no est谩 eliminado
      })
      // Guardar en localStorage despu茅s de agregar
      localStorage.setItem('wishlist', JSON.stringify(state.wishlist))
    },
    removeWishItem(state, id) {
      const item = state.wishlist.find(item => item.id === id)
      if (item) {
        item.deleted = true // Marcar como eliminado en lugar de borrarlo
      }
      // Guardar en localStorage despu茅s de la modificaci贸n
      localStorage.setItem('wishlist', JSON.stringify(state.wishlist))
    },
    restoreWishItem(state, id) {
      const item = state.wishlist.find(item => item.id === id)
      if (item) {
        item.deleted = false // Restaurar el deseo
      }
      // Guardar en localStorage despu茅s de la modificaci贸n
      localStorage.setItem('wishlist', JSON.stringify(state.wishlist))
    }
